import React from "react";
import { Helmet } from 'react-helmet';
const title = "Усний рахунок. Гра для учнів 5 класу";

function Header() {
    return (
        <Helmet>
            <title>{ title }</title>
            <meta name="description" content="{ title }" />	
        </Helmet>
)}

export default Header;